/*** IMPORTS FROM imports-loader ***/
var DEFAULTTIME = 2000;
var APIURL = "//10.242.69.181:8002";
var CROSS = false;
var WITHCRED = false;

'use strict';

import './checkSms.scss';
import Regular from 'regularjs';
import tpl from './checkSms.html';
import request from '../../cache/request';
import Toast from '../../common/toast/toast';
import Confirm from '../../common/confirm/confirm';
import rules from '../../base/rules';
import codeMsg from '../../util/codeMessages';
import util from '../../util/util';


/**
 * 基础组件
 * */
//输入框规则
var ValidateRules = require('@netease/urs-ui').ValidateRules;
// 基本输入框
var InputUI = require('@netease/urs-ui').InputUI;
var PwdInputUI = require('@netease/urs-ui').PwdInputUI;
//  按钮
var button = require('@netease/urs-ui').Button;

import captcha from '../../common/captcha/captcha';

import storage from '../../cache/storage';


const goLoginConfig = {
    info:"该帐号已注册，请直接登录",
    oprate:{
        button2:{
            txt:'去登录',
            func:function(){
                history.go(-1);
            }
        },
    }
};
const goRegConfig = {
    info:"该帐号不存在，请先进行注册",
    oprate:{
        button2:{
            txt:'去注册',
            func:function(){
                history.go(-1);
            }
        },
    }
};

const CURL = 'https://mob.163.com/';
const mbLogin = Regular.extend({
    template: tpl,
    data: {
        rules: rules,
        mobile: '',//手机号
        mobile_h: '',//脱敏手机号
        opType: 'login',//操作类型，login：已存在帐号直接登录，reg：新帐号需注册登录
        channel: '11', // 11-手机帐号短信登录（移动端），12-手机帐号短信注册（移动端）
        agreeClauses: true,//同意服务条款
        smsUpInfo: '###',//上行短信内容
        smsUpNum: '###',//上行短信号码
        smsUpAnt: 'ant-smsup-show',//上行短信动画
        hasPwd: false,
        showCaptchaBox:false, //显示校验码
    },
    init: function () {
        this.initStorage();
        this.data.mobile = this.mobile = this.seStorage['mobile'];
        if(/^\d{1,4}-\d{1,20}$/.test(this.mobile))
            this.data.mobile = '+'+this.mobile;
        this.$update();
    },
    mount: function () {
        if (this.mobile) {
            this.initDom();
            setTimeout(() => {
                this.$state.emit("domReady");
            }, 400);
        } else {
            this.$state.go('app.login', {replace: true});
        }
    },
    leave: function () {
        this.destroy();
    },
    /**
     * 初始化Storage
     * */
    initStorage: function () {
        if (!this.$state.sessionStorage) {
            this.$state.sessionStorage = {};
        }
        this.seStorage = this.$state.sessionStorage;
        storage.init(this.$state.sessionStorage);
        this.data.capKey = this.seStorage['capKey'] || '';
        this.data.hasPwd = this.seStorage['noPwd'] != '1';
    },
    /**
     * 准备页面元素
     * */
    initDom: function () {
        this.mobile_h = this.mobile.replace(/(\d{3})(\d{4})/, "$1****");
        if (this.seStorage['opType'] == 'reg') {
            this.data.opType = 'reg';
            this.data.channel = '12';
        }
        if (this.seStorage['receiveCode']) {
            this.showSmsReceiveCode(this.seStorage['receiveCode']);
        } else if ('201' === this.seStorage['smsCode']) {
            this.$refs.smsBtn.countDown();
            storage.removeSessionStorage('smsCode');
        }
        this.$update();
    },
    /**
     *  条款按钮
     * */
    onClausesCheck: function () {
        this.data.agreeClauses = !this.data.agreeClauses;
        if (!this.data.agreeClauses) {
            Toast.show("您必须同意此条款才能进行注册操作");
        } else {
            Toast.hide();
        }

    },
    /**
     * 获取短信
     * */
    getSms: function () {
        if(this.seStorage['capType']) {
            // 弹出验证码校验
            setTimeout(() => {
                this.showCaptcha();
            }, 0)
        }else{
            this.sendSms();
        }
    },
    /**
     * 弹出验证码信息
     * */
    showCaptcha : function (_capType) {
        if(_capType){
            storage.setSessionStorage('capType',_capType);
            this.data.capType = _capType;
        }
        this.$refs.smsBtn.setStatus('loading');
        this.data.showCaptchaBox = true;
        this.updateCaptcha(this.seStorage['capType']);
    },
    /**
     * 显示验证码
     * */
    updateCaptcha: function (_cpType = 2) {
        this.data.confirmHide = false;
        if (_cpType > 0) {
            this.data.capType = _cpType;
            this.$update();
            if (_cpType == '1') {
                this.$refs.capInput.$emit('getCap', _cpType, '/yd/ncp?pkid=' + this.seStorage['pkid'] + '&pd=' + this.seStorage['pd'] + '&pkht=' + this.seStorage['pkht'] + '&channel=' + this.data.channel + '&topURL=' + encodeURIComponent(location.href));
            } else {
                this.data.sCapWidth = this.$refs.capBox.clientWidth - 20;
                this.$refs.capInput.$emit('getCap', _cpType);
            }
        } else {
            this.data.showCaptchaBox = false;
        }
    },
    /**
     * 取消验证码
     * */
    onCannelCap:function () {
        if(this.capOnHiding)
            return;
        this.capOnHiding = true; //动画上锁
        this.hideCaptcha();
        this.$refs.smsBtn.setStatus('enable');
    },
    /**
     * 隐藏验证码
     * */
    hideCaptcha:function () {
        this.data.confirmHide = true;
        this.$update();
        setTimeout(()=>{
            this.data.showCaptchaBox = false;
            this.capOnHiding = false;
            this.$update();
        },400);
    },
    /**
     * 校验验证码-远程
     * */
    checkCapRemote:(function(){
        var __success = function (_res) {
            var _ret,_capTp;
            if(!!_res){
                _ret = _res.ret;
                if(_ret === '201'){
                    this.sendSms();
                }else{
                    if(_ret === '441'||_ret === '442'){
                        _capTp = 1;
                    }else if(_ret === '444'){
                        _capTp = 2;
                    }else if(_ret === '445'){
                        _capTp = 3;
                    }
                    Toast.show(this.getCodeMsg('NVFCP_'+_ret));
                    this.showCaptcha(_capTp);
                    this.$refs.smsBtn.setStatus('enable');
                }
            }
            this.$refs.btnCheckCap && this.$refs.btnCheckCap.setStatus('enable');
        };
        var __error = function (_res) {
            this.hideCaptcha();
            this.$refs.smsBtn.setStatus('enable');
            this.$refs.btnCheckCap && this.$refs.btnCheckCap.setStatus('enable');
        };
        return function(_type,_val){
            var _url,_method = 'GET',_contentType,
                _data = {
                    pd  : this.seStorage['pd'],
                    pkid  : this.seStorage['pkid'],
                    pkht  : this.seStorage['pkht'],
                    cap  : _val,
                    channel  : this.data.channel ,
                    topURL  : location.href,
                    un  : this.mobile
                };
            switch (_type){
                case 1 :
                    _url = '/yd/nvfcp';
                    _method = 'POST';
                    _contentType = 'application/x-www-form-urlencoded';
                    break;
                case 2 :
                    _url = '/yd/nvftcp';
                    _data.capkey = this.data.capKey;
                    break;
                case 3 :
                    _url = '/yd/nvfccp';
                    _data.capkey = this.data.capKey;
                    break;
                default:
                    Toast.show('验证码加载出错');
                    break;
            }
            request({
                url:_url,
                method:_method,
                contentType:_contentType,
                data:_data,
                success:__success.bind(this),
                error:__error.bind(this)
            })
        }
    })(),
    /**
     * 校滑块、点击验验证码
     * */
    checkSCap:function (_cdata) {
        if(!!_cdata.value){
            this.checkCapRemote(_cdata.capType,_cdata.value)
        }
    },
    /**
     * 校验图片验证码
     * */
    onCheckCap:function () {
        this.$refs.capValidation.$once('validate', (conclusion) => {
            if(conclusion.success) {
                this.$refs.btnCheckCap.setStatus('loading');
                this.checkCapRemote(1,this.$refs.capInput.getValue());
            }else {
                Toast.show(conclusion.message || '未知错误');
            }
        }).validate();
    },
    /**
     * 发送短信
     * */
    sendSms: (function () {
        var __success = function (_res) {
            var _ret,_capTp;
            if (!!_res) {
                _ret = _res.ret;
                if(['441','444','445'].indexOf(_ret) != -1){
                    if (_ret === '441') {
                        _capTp = 1;
                    } else if (_ret === '444') {
                        _capTp = 2;
                    } else if (_ret === '445') {
                        _capTp = 3;
                    }
                    this.showCaptcha(_capTp);
                    Toast.show(this.getCodeMsg('SSMS_' + _ret));
                }else {
                    this.hideCaptcha();
                    if (_ret === '201') {
                        this.data.smsUp = false;
                        this.$refs.smsBtn.countDown();
                        return;
                    } else if (_ret === '407' || _ret === '420') {
                        //帐号已经存在，切换到登录
                        Confirm.show(_ret === '407' ? goLoginConfig : goRegConfig);
                    } else if (_ret === '411' && _res.receiveCode) {
                        //上行短信
                        storage.setSessionStorage('receiveCode', _res.receiveCode);
                        this.showSmsReceiveCode(_res.receiveCode);
                    } else if (_ret === '422' || _ret === '602') {
                        //帐号已被锁定、冻结
                        Confirm.show( _ret === '422'?this.$state.AccConfig['lockedAccConfig']:this.$state.AccConfig['frozenAccConfig']);
                    } else{
                        if (_ret === '401' && _res.dt == '10') {
                            _ret += '_10';
                        }
                        Toast.show(this.getCodeMsg('SSMS_' + _ret));
                    }
                    this.$refs.smsBtn.setStatus('enable');
                }
            }
            this.$update();
        };
        var __error = function (_res) {
            this.hideCaptcha();
            this.$refs.smsBtn.setStatus('enable');
        };
        return function () {
            storage.removeSessionStorage('receiveCode');
            var _url = '/yd/nlssms',
                _data = {
                    pd: this.seStorage['pd'],
                    pkid: this.seStorage['pkid'],
                    pkht: this.seStorage['pkht'],
                    un: this.mobile,
                    topURL: location.href,
                    channel: '11'
                };
            if (this.data.opType == 'reg') {
                _url = '/yd/nregssms';
                _data.channel = '12';
            }
            request({
                url: _url,
                method: 'GET',
                data: _data,
                success: __success.bind(this),
                error: __error.bind(this)
            })
        }
    })(),
    /**
     * 显示上行短信
     * */
    showSmsReceiveCode: function (_receiveCode) {
        var _tpCf = _receiveCode.split(',') || [];
        this.data.smsUpInfo = _tpCf[0];
        this.data.smsUpNum = _tpCf[1];
        if (this.data.smsUp == true) {
            this.data.smsUp = false;
            this.$update();
        }
        this.data.smsUp = true;
    },
     /**
     * Next
     * */
    doLogin: function (_event) {
        this.$refs.validation.$once('validate', (conclusion) => {
            if (conclusion.success) {
                if (this.data.agreeClauses) {
                    this.loginRequest();
                } else {
                    Toast.show("请先勾选用户条款才能注册");
                }
            } else {
                Toast.show(conclusion.message || '未知错误');
            }
        }).validate();
    },
    /**
     * login请求
     * */
    loginRequest: function () {
        this.$refs.btnLogin.setStatus('loading');
        if(this.data.opType == 'reg'){
            this.checkSms();
        }else {
            this.getLoginTK();
        }
    },
    /**
     * 获取登录凭证
     * */
    getLoginTK: (function () {
        var __success = function (_res) {
            var _ret;
            if (!!_res) {
                _ret = _res.ret;
                if (_ret === '201') {
                    this.data.loginTK = _res.tk;
                    this.checkSms();
                } else {
                    if (_ret === '401' && _res.dt == '10') {
                        _ret += '_10';
                    }
                    Toast.show(this.getCodeMsg('NGT_' + _ret));
                }
            }
            this.$refs.btnLogin.setStatus('enable');
        };
        var __error = function (_res) {
            this.$refs.btnLogin.setStatus('enable');
        };
        return function () {
            this.data.loginTK = '';
            var _data = {
                pd: this.seStorage['pd'],
                pkid: this.seStorage['pkid'],
                pkht: this.seStorage['pkht'],
                channel: this.data.channel ,
                un: this.mobile,
                topURL: location.href
            };
            request({
                url: '/yd/nlgt',
                method: 'GET',
                data: _data,
                success: __success.bind(this),
                error: __error.bind(this)
            })
        }
    })(),
    /**
     * 校验短信
     * */
    checkSms: (function () {
        var __success = function (_res) {
            var _ret,_urlParams,_curl;
            if (!!_res) {
                _ret = _res.ret;
                if (_ret === '201') {
                    //登录成功
                    _urlParams = {
                        'mobile':this.mobile|| ''
                    };
                    _curl = util.checkCUrl(this.seStorage['curl'])?this.seStorage['curl']:CURL;
                    _curl = util.urlChangeParams(_curl,_urlParams);
                    if(_res.nextUrls){
                        util.insetFrames(_res.nextUrls,function(){
                            location.href = _curl;
                        }.bind(this),2);
                    }else{
                        location.href = _curl;
                    }
                }else if (_ret === '422' || _ret === '602') {
                    //帐号已被锁定、冻结
                    Confirm.show( _ret === '422'?this.$state.AccConfig['lockedAccConfig']:this.$state.AccConfig['frozenAccConfig']);
                } else if (_ret === '407' || _ret === '420') {
                    //帐号已经存在，切换到登录
                    Confirm.show(_ret === '407' ? goLoginConfig : goRegConfig);
                } else {
                    if(_res.dt && ['401_10','401_16','401_17','412_01','413_01','413_03','417_01','418_01','419_01','419_02'].indexOf(_ret + '_' + _res.dt) != -1){
                        _ret += '_'+_res.dt;
                    }
                    Toast.show(this.getCodeMsg('VFSMS_' + _ret));
                }
            }
            this.$refs.btnLogin.setStatus('enable');
        };
        var __error = function (_res) {
            this.$refs.btnLogin.setStatus('enable');
        };
        return function () {
            var _data = {
                pd: this.seStorage['pd'],
                pkid: this.seStorage['pkid'],
                pkht: this.seStorage['pkht'],
                channel: this.data.channel ,
                un: this.mobile,
                sms: this.$refs.smsInput.getValue(),
                topURL: location.href,
                domains: this.seStorage['domains'] || ''
            };
            if(this.data.opType == 'reg' && this.data.hasPwd ){
                _data.pw = util.encrypt(this.$refs.pwdInput.getValue(),this.mobile);
            }else if(this.data.opType == 'login'){
                _data.tk = this.data.loginTK;
            }
            request({
                url: this.data.opType == 'reg' ? '/yd/nregvfsms' : '/yd/nlvfsms',
                method: 'POST',
                data: _data,
                success: __success.bind(this),
                error: __error.bind(this)
            })
        }
    })(),
    /**
     * 根据code获取错误文案
     * */
    getCodeMsg: function (_key) {
        return codeMsg[_key] || "未知错误";
    }

});

module.exports = mbLogin;




