/*** IMPORTS FROM imports-loader ***/
var DEFAULTTIME = 2000;
var APIURL = "//10.242.69.181:8002";
var CROSS = false;
var WITHCRED = false;

import './index.scss';
import restate from 'regular-state';

/**
 * 模块
 * */
import App from '../../module/app/app';
import mbLogin from '../../module/mbLogin/mbLogin';
import pwdLogin from '../../module/pwdLogin/pwdLogin';
import checkSms from '../../module/checkSms/checkSms';

import Promise from 'es6-promise';
if (!window.Promise)
    window.Promise = Promise;
// es6 api polyfill
require('core-js/fn/array/from');
require('core-js/fn/array/find');
require('core-js/fn/object/assign');
require('core-js/fn/string/includes');
require('core-js/fn/string/starts-with');
require('core-js/fn/string/ends-with');

const manager = restate();
manager.state({
    'app': {
        url:"",
        view: App
    },
    'app.login': {
        url:"",
        view: mbLogin
    },
    'app.pwdLogin': {
        view: pwdLogin
    },
    'app.checkSms': {
        view: checkSms
    }
}).on("notfound", function(){
    this.go('app.login',{replace:true}); // if not found
}).start({
    view: document.getElementById('app')
});




