var DEFAULTTIME = 2000;
var APIURL = "//10.242.69.181:8002";
var CROSS = false;
var WITHCRED = false;

'use strict'

import './button.scss'
import tpl from './button.rgl'

var Base = require('../base/component');
/**
 * 按钮状态：enable  disabled  
 * loading
 * 参数shuo
 */
var Button = Base.extend({
    name: 'Button',
    template: tpl,
    config() {
        this.defaults({
            enableTip: '获取验证码',
            retryTip: '重新获取',
            countingTip: '[count]s后重发',
            status: 'enable',
            count: 30,
            disabled: 0,
            tip:'',
            loading_clazz:'i-icon i-icon-loading',
            enableLoading:1,
        });
        this.supr();  
        this.watch();
    },
    watch() {
        this.$watch('status',(value)=>{
            this.data.disabled = (value === 'disabled');
            this.$update();
        })
    },
    init() {
        this.data.tip = this.data.enableTip;
    },
    _onClick (_event){
        _event.preventDefault();
        this.$emit('click',{
            sender: this,
            status: this.data.status
        })
    },
    cancelCount (){
        clearTimeout(this.timer);
    },
    setStatus(status,tip){
        this.data.status = status;
        if(tip){
            this.data.tip = tip;
        }
        this.$update();
    },
    // 倒计时count-1=>0
    countDown: (function (){
        var _count = 10
        
        var t = function(){
            this.timer = setTimeout(()=>{
                _count --;
                this.data.tip = this.data.countingTip.replace('[count]',_count);
                this.$update();
                
                if(_count < 0){
                    this.$emit('countend',{
                        sender:this,
                    })
                    this.setStatus('enable',this.data.enableTip);
                    this.$update();
                    clearTimeout(this.timer);
                    return;
                }
                t.apply(this);
            },1000)
        }
        return function(){
            this.$emit('countbegin',{
            sender:this,
            });
            _count = this.data.count;
            this.data.status = 'disabled';
            _count --;
            this.data.tip = this.data.countingTip.replace('[count]',_count);
            this.$update();
            t.apply(this);
        }
    })()
});

module.exports = Button;




