var CROSS = false;
var WITHCRED = false;

/**
 * 基础组件-验证码，封装信息安全部验证码组件
 * @auth hzhuangdi(hzhuangdi@corp.netease.com)
 * 
 * public属性:
 * @param {string} data.value
 * =>  滑块校验结果
 * 
 * private属性:
 * @param {string} data._tip
 * =>  滑块文案提示，默认为错误样式
 * 
 * public API:
 * getCaptcha  如果未实例过滑块对象则合并传入参数并实例化滑块对象，否则调用滑块refresh方法
 * @param {object} _opts  
 * => 与传给滑块组件的默认参数进行覆盖合并，具体参考滑块文档
 * @return  滑块实例
 * showError  显示滑块错误提示
 * @param {string} _msg
 * => 提示文案
 * @return 
 * clearError  隐藏滑块错误提示
 * @return
 * 
 * Events:
 * verify   滑块校验回调通知
 * init    滑块初始化成功回调通知
 * initError    滑块初始化失败回调通知
 */
'use strict'

import Base from '../base/component';
import tpl from './captcha.rgl';
import './captcha.scss';
// ec.js自行引入
// import './ec.js';

var Validation = require('rgui-ui-field').Validation;

const Captcha = Base.extend({
    template:tpl,
    name:'Captcha-Dun',
    config(){
        this.defaults({
            value:'',  //值为未验证 验证成功 验证失败
            _tip:'',
            vType:0
        });
         // 查找validation，修正$outer
        // TODO $outer不是validation的情况下，是否要修正$outer？
        let $outer = this.$outer;
        while ($outer) {
            if ($outer instanceof Validation) {
            $outer.fields.push(this);
            break;
            }
            $outer = $outer.$outer;
        }
    },
    validate(trigger = 'submit'){
        
        const value = this.data.value;
        const rules = this.data.rules.filter((rule) => (rule.trigger + '+submit').includes(trigger));

         Validation.validate(value, rules, (result) => {
            if (result.firstRule && !(result.firstRule.mute || '').includes(trigger))
                // @TODO tip根据结果？
                this.showError(result.message);
            else
                this.clearError();
            // 先不维护_state
            // this.data._state = this.data.state = result.success ? 'success' : 'error';
            this.data.state = result.success ? 'success' : 'error';
            
            this.$update();

            /**
             * @event validate 验证表单域时触发
             * @property {object} sender 事件发送对象
             * @property {string} trigger 验证触发方式
             * @property {boolean} success 验证是否通过
             * @property {string} message 验证不通过时的消息
             * @property {object} firstRule 第一条验证不通过的规则
             */
            this.$emit('validate', Object.assign({
                sender: this,
                trigger,
            }, result));
        });
    },
    getCaptcha(_opts){
        var _capOpt = {
       
        "element": "Scaptcha", // 可以是验证码容器id，也可以是HTMLElement
        "captchaId": _opts.captchaId, // 这里填入申请到的验证码id
        "width": 342, // 验证码组件显示宽度,
        "mode": "float",
        "captchaType": _opts.captchaType,
        "initCallback": (r)=>{
            this._initCallback(r);
        },
        "verifyCallback":(r)=>{
            this._verifyCallBack(r);
        }};
 
        this.data.vType = _opts.captchaType;

        if(!this._captcha){
            this._captcha = new window.NECaptcha(_capOpt);
        }else{
            this._captcha.refresh(_opts);
            // 实例存在则刷新
            this.$emit('capinit',{
            sender:this,
            });
        }
        return this._captcha;
    },
    refresh(_opts){
        this.data.value = 0;
        if(_opts){
            var _o = {"captchaType":_opts.captchaType||this.data.vType};
        }
        if(!this._captcha){
            return;
        }else{
            this._captcha.refresh(_o);
        }
    },
    showError(_msg){
        var _t = this.getCt()===2?'滑块':'点击';
        this.data._tip = _msg.replace('#',_t);
        this.$update();
    },
    clearError(){
        this.data._tip = '';
        this.$update();        
    },
    getCp(){
        return this._captcha.getValidate();
    },
    getCt(){
        return this.data.vType;
    },
    _verifyCallBack(_res){
        this.data.value = _res.value===false?0:1;
        if(_res.value === true){
            this.clearError();
        }else{
            
            this.showError('验证失败，请重新验证#验证码');
        }
        // 抛出verify事件
        this.$emit('verify',{
            sender:this,
            value: _res.value,
            result: _res
        });
    },
    _initCallback(){
        // 抛出init事件
        this.$emit('capinit',{
            sender:this,
        });
    },
    // _initErrorHandler(){
    //     // 抛出initError事件
    //     this.$emit('capinitError',{
    //         sender:this,
    //     });
    // }
})

module.exports = Captcha;




