/*** IMPORTS FROM imports-loader ***/
var DEFAULTTIME = 2000;
var APIURL = "//10.242.69.181:8002";
var CROSS = false;
var WITHCRED = false;

'use strict';

import './pwdLogin.scss';
import Regular from 'regularjs';
import tpl from './pwdLogin.html';
import request from '../../cache/request';
import Toast from '../../common/toast/toast';
import Confirm from '../../common/confirm/confirm';
import rules from '../../base/rules';
import codeMsg from '../../util/codeMessages';

import storage from '../../cache/storage';

/**
 * 基础组件
 * */
//输入框规则
var ValidateRules = require('@netease/urs-ui').ValidateRules;
// 基本输入框
var InputUI = require('@netease/urs-ui').InputUI;
var PwdInputUI = require('@netease/urs-ui').PwdInputUI;
//  按钮
var button = require('@netease/urs-ui').Button;

import captcha from '../../common/captcha/captcha';
import mbInput from '../../common/mbInput/mbInput';
import util from '../../util/util';


const CURL = 'https://mob.163.com/';

const mbLogin = Regular.extend({
    template:tpl,
    data:{
        rules:rules,
        channel:13
    },
    init:function(){
        this.initStorage();
        if (!this.seStorage['pd']||!this.seStorage['pkid']||!this.seStorage['pkht']||!this.seStorage['capKey']) {
            setTimeout(()=>{this.$state.go('app.login', {replace: true});},100)
        }else if(this.seStorage['curl']){
            this.remoteInit();
        }else{
            this.$state.emit("domError","CURL参数错误");
        }
    },
    mount:function () {
        if(this.data.capType>0){
            this.showCaptcha(this.data.capType);
        }
    },
    /**
     * 初始化Storage
     * */
    initStorage:function () {
        if (!this.$state.sessionStorage) {
            this.$state.sessionStorage = {};
        }
        this.seStorage = this.$state.sessionStorage;
        storage.init(this.$state.sessionStorage);
        this.data.capKey = this.seStorage['capKey'] || '';
    },
    /**
     * 初始化请求
     * */
    remoteInit:(function(){
        var __success = function (_res) {
            var _ret,_msg;
            if(!!_res){
                _ret = _res.ret;
                if(_ret === '201'){
                    this.showCaptcha(_res.capFlag&&_res.capFlag>1?_res.capFlag-2:_res.capFlag);
                    setTimeout(()=>{this.$state.emit("domReady");},200)
                }else{
                    // Toast.show(this.getCodeMsg('NINI_'+_ret));
                    this.$state.emit("domError");
                }
            }
        };
        var __error = function (_res) {
            this.$state.emit("domError");
        };
        return function(){
            var _data = {
                pd  : this.seStorage['pd'],
                pkid  : this.seStorage['pkid'],
                pkht  : this.seStorage['pkht'],
                channel  : this.data.channel,
                topURL  : location.href,
            };
            request({
                url:'/yd/nini',
                method:'GET',
                data:_data,
                success:__success.bind(this),
                error:__error.bind(this)
            })
        }
    })(),
    /**
     * 显示验证
     * */
    showCaptcha:function (_cpType) {
        if(_cpType>0) {
            this.data.hasCaptcha = true;
            this.data.capType = _cpType;
            this.$update();
            if(_cpType == '1') {
                this.$refs.capInput.$emit('getCap', _cpType,'/yd/ncp?pkid=' + this.seStorage['pkid'] + '&pd=' + this.seStorage['pd'] + '&pkht=' + this.seStorage['pkht'] + '&channel=' + this.data.channel + '&topURL=' + encodeURIComponent(location.href));
            }else{
                this.$refs.capInput.$emit('getCap', _cpType);
            }
        }else{
            this.data.hasCaptcha = false;
        }
    },
    /**
     * 校验验证码-远程
     * */
    checkCapRemote:(function(){
        var __success = function (_res) {
            var _ret,_msg;
            if(!!_res){
                _ret = _res.ret;
                if(_ret === '201'){
                    if(this.data.capType == '1') {
                        this.getLoginTK();
                    }
                }else{
                    if(_ret === '441'||_ret === '442'){
                        this.data.capType = 1;
                    }else if(_ret === '444'){
                        this.data.capType = 2;
                    }else if(_ret === '445'){
                        this.data.capType = 3;
                    }
                    _msg = this.getCodeMsg('NVFCP_'+_ret);
                    Toast.show(_msg);
                    this.showCaptcha(this.data.capType);
                    this.$refs.btnPwdLogin.setStatus('enable');
                }
            }
        };
        var __error = function (_res) {
            this.$refs.btnPwdLogin.setStatus('enable');
        };
        return function(_type,_val){
            var _url,_method = 'GET',_contentType,
                _data = {
                    pd  : this.seStorage['pd'],
                    pkid  : this.seStorage['pkid'],
                    pkht  : this.seStorage['pkht'],
                    cap  : _val,
                    channel  : this.data.channel ,
                    topURL  : location.href,
                    un  : this.$refs.mbInput.getValue()
                };
            switch (_type){
                case 1 :
                    _url = '/yd/nvfcp';
                    _method = 'POST';
                    _contentType = 'application/x-www-form-urlencoded';
                    break;
                case 2 :
                    _url = '/yd/nvftcp';
                    _data.capkey = this.data.capKey;
                    break;
                case 3 :
                    _url = '/yd/nvfccp';
                    _data.capkey = this.data.capKey;
                    break;
                default:
                    Toast.show('验证码加载出错');
                    break;
            }
            request({
                url:_url,
                method:_method,
                contentType:_contentType,
                data:_data,
                success:__success.bind(this),
                error:__error.bind(this)
            })
        }
    })(),
    /**
     * 校滑块、点击验验证码
     * */
    checkSCap:function (_cdata) {
        if(!!_cdata.value){
            this.checkCapRemote(_cdata.capType,_cdata.value)
        }
    },
    /**
     * 滑块、图片验证失败
     * */
    sCapError:function (_cdata) {
        var _errMsg;
        switch (_cdata.capType){
            case 2:
                _errMsg = '滑动校验失败，请重新滑动';
                break;
            case 3:
                _errMsg = '点击校验失败，请重新点击';
                break;
            default:
                _errMsg = '校验失败，请重新校验';
        }
        Toast.show(_errMsg);
    },
    /**
     * Next 
     * */
    onNext : function () {
        this.$refs.validation.$once('validate', (conclusion) => {
            if(conclusion.success) {
                this.$refs.btnPwdLogin.setStatus('loading');
                if(this.data.hasCaptcha && this.data.capType == '1') {
                    this.checkCapRemote(1,this.$refs.capInput.getValue());
                }else {
                    this.getLoginTK()
                }
            }else {
                Toast.show(conclusion.message || '未知错误');
            }
        }).validate();
    },
    /**
     * 获取登录凭证
     * */
    getLoginTK: (function () {
        var __success = function (_res) {
            var _ret;
            if (!!_res) {
                _ret = _res.ret;
                if (_ret === '201') {
                    this.data.loginTK = _res.tk;
                    this.loginReq();
                } else {
                    if (_ret === '401' && _res.dt == '10') {
                        _ret += '_10';
                    }
                    Toast.show(this.getCodeMsg('NGT_' + _ret));
                }
            }
            this.$refs.btnPwdLogin.setStatus('enable');
        };
        var __error = function (_res) {
            this.$refs.btnPwdLogin.setStatus('enable');
        };
        return function () {
            this.data.loginTK = '';
            var _data = {
                pd: this.seStorage['pd'],
                pkid: this.seStorage['pkid'],
                pkht: this.seStorage['pkht'],
                channel: this.data.channel ,
                un: this.$refs.mbInput.getValue(),
                topURL: location.href
            };
            request({
                url: '/yd/nlgt',
                method: 'GET',
                data: _data,
                success: __success.bind(this),
                error: __error.bind(this)
            })
        }
    })(),
    /**
     * 登录请求
     * */
    loginReq:(function(){
        var __success = function (_res) {
            var _ret,_urlParams,_curl;
            if(!!_res){
                _ret = _res.ret;
                if (_ret === '201') {
                    //登录成功
                    _urlParams = {
                        'mobile':this.$refs.mbInput.getValue() || ''
                    };
                    _curl = util.checkCUrl(this.seStorage['curl'])?this.seStorage['curl']:CURL;
                    _curl = util.urlChangeParams(_curl,_urlParams);
                    if(_res.nextUrls){
                        util.insetFrames(_res.nextUrls,function(){
                            location.href = _curl;
                        }.bind(this),2);
                    }else{
                        location.href = _curl;
                    }
                } else if(_ret === '422' || _ret === '602'){
                    //帐号已被锁定、冻结
                    Confirm.show( _ret === '422'?this.$state.AccConfig['lockedAccConfig']:this.$state.AccConfig['frozenAccConfig']);
                    this.showCaptcha(this.data.capType );
                } else {
                    if(_ret === '441'||_ret === '442'){
                        this.data.capType = 1;
                    }else if(_ret === '444'){
                        this.data.capType = 2;
                    }else if(_ret === '445'){
                        this.data.capType = 3;
                    }else if(_res.dt && ['401_10','412_01','413_01','413_03','414_01','417_01','418_01','419_01','419_02'].indexOf(_ret + '_' + _res.dt) != -1){
                        _ret += '_'+_res.dt;
                    }
                    this.showCaptcha(this.data.capType);
                    Toast.show(this.getCodeMsg('NLPWD_' + _ret));
                }
            }
            this.$refs.btnPwdLogin.setStatus('enable');
        };
        var __error = function (_res) {
            this.data.capType>0 && this.showCaptcha(this.data.capType );
            this.$refs.btnPwdLogin.setStatus('enable');
        };
        return function(){
            var _data = {
                pd: this.seStorage['pd'],
                pkid: this.seStorage['pkid'],
                pkht: this.seStorage['pkht'],
                channel: this.data.channel ,
                un: this.$refs.mbInput.getValue(),
                tk: this.data.loginTK,
                pw: util.encrypt2(this.$refs.pwdInput.getValue()),
                topURL: location.href,
                domains: this.seStorage['domains'] || ''
            };
            request({
                url:'/yd/nlpwd',
                method:'POST',
                data:_data,
                success:__success.bind(this),
                error:__error.bind(this)
            })
        }
    })(),
    getCodeMsg :function (_key) {
        return codeMsg[_key] || "未知错误";
    }
});

module.exports = mbLogin;




