/*** IMPORTS FROM imports-loader ***/
var DEFAULTTIME = 2000;
var APIURL = "//10.242.69.181:8002";
var CROSS = false;
var WITHCRED = false;

'use strict'

import './toast.scss';
import Regular from 'regularjs';
import tpl from './toast.html';
const TIME = 3000;

let Toast = Regular.extend({
    template:tpl,
    name:'Toast',
    init:function(_data){
        this.supr(_data);
    },
    show:function(_toastCnt,_t = TIME){
        this.sto && this.hide(1);
        this.data.toastCnt = _toastCnt;
        this.$update();
        this.sto = setTimeout(()=>{
            this.hide();
        },_t);
    },
    hide:function (_style) {
        clearTimeout(this.sto);
        if(_style === 1) {
            this.data.toastCnt = '';
        }else{
            this.data.toastHide = true;
            setTimeout(()=>{
                this.data.toastCnt = '';
                this.data.toastHide = false;
                this.$update();
            },200);
        }
        this.$update();
    }
});

module.exports =  new Toast({}).$inject(document.body);




