/*** IMPORTS FROM imports-loader ***/
var DEFAULTTIME = 2000;
var APIURL = "//10.242.69.181:8002";
var CROSS = false;
var WITHCRED = false;

'use strict';

var ValidateRules = {
    mobile: [
        {type: 'isRequired', trigger: 'submit', message: '请填写手机号'},
        {type: 'is', trigger: 'submit', message: '请填写正确格式的手机号', options: /^(13|14|15|17|18)\d{9}$/}
    ],
    i18n_mobile: [
        {type: 'isRequired', trigger: 'submit', message: '请填写手机号'},
        {type: 'is', trigger: 'submit', message: '请填写正确格式的手机号', options: /^\d{1,4}-\d{1,20}$/}
    ],
    sms: [
        {type: 'isRequired', trigger: 'submit', message: '请输入短信验证码'},
        {
            type: 'is', trigger: 'submit', message: '请输入正确格式的短信验证码', options: /^[0-9]{6}$/
        }
    ],
    captcha: [
        [
            {type: 'isRequired', trigger: 'submit', message: '请输入正确的验证码'},
            {type: 'is', trigger: 'submit', message: '验证码格式错误', options: /^[0-9a-zA-Z]{4}$/}
        ],
        [{type: 'isRequired', trigger: 'submit', message: '请滑块滑块完成验证码验证'}],
        [{type: 'isRequired', trigger: 'submit', message: '请点击验证码完成验证'}],
    ],
    password: [
        {type: 'isRequired', trigger: 'submit', message: '请输入密码'},
        {type: 'isNot', trigger: 'blur+submit', message: '密码不允许包含空格', options: /\s/},
        {type: 'isNot', trigger: 'blur+submit', message: '密码不允许包含特殊字符', options: /[^\x21-\x7e]/},
        {type: 'isLength', trigger: 'blur+submit', message: '请输入6-16位密码', options: {min: 6, max: 16}},
        {
            type: 'method', trigger: 'blur+submit', message: '密码过于简单，请重新输入', options: (value) => {
            var simpleList = ['123456', '123456789', '12345678', '123123', '5201314', '1234567', '7758521', '654321', '1314520', '123321', '1234567890', '147258369', '123654', '5211314', 'woaini', '1230123', '987654321', '147258', '123123123', '7758258', '520520', '789456', '456789', '159357', '112233', '1314521', '456123', '110110', '521521', 'zxcvbnm', '789456123', '0123456789', '0123456', '123465', '159753', 'qwertyuiop', '987654', '115415', '1234560', '123000', '123789', '100200', '963852741', '121212', '111222', '123654789', '12301230', '456456', '741852963', 'asdasd', 'asdfghjkl', '369258', '863786', '258369', '8718693', '666888', '5845201314', '741852', '168168', 'iloveyou', '852963', '4655321', '102030', '147852369', '321321'];
            return (simpleList.indexOf(value) === -1) ? 1 : 0;
        }
        },
        //  判断帐号和密码是否一致的流程业务相关性比较强，建议各个项目内扩展自行实现
        {
            type: 'method', trigger: 'blur+submit', message: '密码不能与帐号一致', options: (value) => {
            // 各个项目ssn的值获取途径可能不同
            var ssn = '123@163.com';
            return !(value == ssn || value == ssn.split('@')[0])
        }
        }
    ],
    password4login: [
        {type: 'isRequired', trigger: 'submit', message: '请输入密码'},
    ]
}

module.exports = ValidateRules;




