/*** IMPORTS FROM imports-loader ***/
var DEFAULTTIME = 2000;
var APIURL = "//10.242.69.181:8002";
var CROSS = false;
var WITHCRED = false;

'use strict';

import './mbLogin.scss';
import Regular from 'regularjs';
import tpl from './mbLogin.html';
import request from '../../cache/request';
import Toast from '../../common/toast/toast';
import Confirm from '../../common/confirm/confirm';
import rules from '../../base/rules';
import codeMsg from '../../util/codeMessages';

import storage from '../../cache/storage';
import util from '../../util/util';

/**
 * 基础组件
 * */
//输入框规则
// var ValidateRules = require('@netease/urs-ui').ValidateRules;
// 基本输入框
var InputUI = require('@netease/urs-ui').InputUI;
//  按钮
var button = require('@netease/urs-ui').Button;

import captcha from '../../common/captcha/captcha';
import mbInput from '../../common/mbInput/mbInput';


const pd = 'urs',
    pkid = 'SmjBWzI',
    pkht = 'dl.reg.163.com',
    capkey = 'b1d9f2c000e8cce0cba444e49ee12f2d';
const DomainsRegExp = new RegExp('^[a-zA-Z0-9\.\,]*$');
const mbLogin = Regular.extend({
    template:tpl,
    data:{
        rules:rules,
        capKey:capkey,
        domains:'',//需求写入登录cookie的域名
        capType:''
    },
    init:function(){
        this.initStorage();
        this.initConfig();
        // if(!this.data.curl || !util.checkCUrl(this.data.curl)) {
        //     this.$state.emit("domError","参数curl错误");
        // }else if(this.data.domains && !DomainsRegExp.test(this.data.domains)){
        //     this.$state.emit("domError","参数domains错误");
        // }else{
            this.remoteInit();
        // }
    },
    mount:function () {
        if(this.data.capType>0){
            this.showCaptcha(this.data.capType);
        }
    },
    /**
     * 初始化Storage
     * */
    initStorage:function () {
        storage.clearSessionStorage();
        if(!this.$state.sessionStorage) {
            this.$state.sessionStorage = {};
        }
        storage.init(this.$state.sessionStorage);
    },
    /**
     * 初始化Config
     * */
    initConfig:function () {
        //多域参数
        this.data.domains = util.getUrlSearch('domains') || '';
        this.data.curl = /^http[s]?:/i.test(util.getUrlSearch('curl'))?util.getUrlSearch('curl'):"";
        this.data.noPwd = util.getUrlSearch('nopwd') || 0;

        storage.setSessionStorage('pd',pd);
        storage.setSessionStorage('pkid',pkid);
        storage.setSessionStorage('pkht',pkht);
        storage.setSessionStorage('capKey',capkey);
        this.data.domains &&  storage.setSessionStorage('domains',this.data.domains);
        this.data.curl &&  storage.setSessionStorage('curl',this.data.curl);


    },
    /**
     * 初始化请求
     * */
    remoteInit:(function(){
        var __success = function (_res) {
            var _ret;
            if(!!_res){
                _ret = _res.ret;
                if(_ret === '201'){
                    this.showCaptcha(_res.capFlag&&_res.capFlag>1?_res.capFlag-2:_res.capFlag);
                    setTimeout(()=>{this.$state.emit("domReady");},200)
                }else{
                    this.$state.emit("domError");
                }
            }
        };
        var __error = function (_res) {
            // this.$state.emit("domError");
            this.$state.emit("domReady")
        };
        return function(){
            var _data = {
                pd  : pd,
                pkid  : pkid,
                pkht  : pkht,
                channel  : '10',
                topURL  : location.href,
            };
            request({
                url:'/yd/nini',
                method:'GET',
                noToast:1,
                data:_data,
                success:__success.bind(this),
                error:__error.bind(this)
            })
        }
    })(),
    /**
     * 显示验证
     * */
    showCaptcha:function (_cpType) {
        if(_cpType>0) {
            this.data.hasCaptcha = true;
            this.data.capType = _cpType;
            this.$update();
            if(_cpType == '1') {
                this.$refs.capInput.$emit('getCap', _cpType,'/yd/ncp?pkid='+pkid+'&pd='+pd+'&pkht='+pkht+'&channel=10&topURL='+encodeURIComponent(location.href));
            }else{
                this.$refs.capInput.$emit('getCap', _cpType);
            }
        }else{
            this.data.hasCaptcha = false;
        }
    },
    /**
     * 校验验证码-远程
     * */
    checkCapRemote:(function(){
        var __success = function (_res) {
            var _ret;
            if(!!_res){
                _ret = _res.ret;
                if(_ret === '201'){
                    if(this.data.capType == '1') {
                        this.checkAcc();
                    }
                }else{
                    if(_ret === '441'||_ret === '442'){
                        this.data.capType = 1;
                    }else if(_ret === '444'){
                        this.data.capType = 2;
                    }else if(_ret === '445'){
                        this.data.capType = 3;
                    }
                    Toast.show(this.getCodeMsg('NVFCP_'+_ret));
                    this.showCaptcha(this.data.capType);
                    this.$refs.btnGetSms.setStatus('enable');

                }
            }
        };
        var __error = function (_res) {
            this.showCaptcha(this.data.capType);
            this.$refs.btnGetSms.setStatus('enable');
        };
        return function(_type,_val){
            var _url,_method = 'GET',_contentType='',
                _data = {
                pd  : pd,
                pkid  : pkid,
                pkht  : pkht,
                cap  : _val,
                channel  : '10',
                topURL  : location.href,
                un  : this.$refs.mbInput.getValue(),
            };
            switch (_type){
                case 1 :
                    _url = '/yd/nvfcp';
                    _method = 'POST';
                    _contentType = 'application/x-www-form-urlencoded';
                    break;
                case 2 :
                    _url = '/yd/nvftcp';
                    _data.capkey = capkey;
                    break;
                case 3 :
                    _url = '/yd/nvfccp';
                    _data.capkey = capkey;
                    break;
                default:
                    Toast.show('验证码加载出错');
                    break;
            }
            request({
                url:_url,
                method: _method,
                data:_data,
                contentType:_contentType,
                success:__success.bind(this),
                error:__error.bind(this)
            })
        }
    })(),
    /**
     * 校滑块、点击验验证码
     * */
    checkSCap:function (_cdata) {
        if(!!_cdata.value){
            this.checkCapRemote(_cdata.capType,_cdata.value)
        }
    },
    /**
     * 滑块、图片验证失败
     * */
    sCapError:function (_cdata) {
        var _errMsg;
        switch (_cdata.capType){
            case 2:
                _errMsg = '滑动校验失败，请重新滑动';
                break;
            case 3:
                _errMsg = '点击校验失败，请重新点击';
                break;
            default:
                _errMsg = '校验失败，请重新校验';
        }
        Toast.show(_errMsg);
    },
    /**
     * Next 
     * */
    onNext : function () {
        this.$refs.validation.$once('validate', (conclusion) => {
            if(conclusion.success) {
                this.$refs.btnGetSms.setStatus('loading');
                if(this.data.hasCaptcha && this.data.capType == '1') {
                    this.checkCapRemote(1,this.$refs.capInput.getValue());
                }else {
                    this.checkAcc();
                }
            }else {
                Toast.show(conclusion.message || '未知错误');
            }
        }).validate();
    },
    /**
     * 检查帐号是否存在
     * */
    checkAcc:(function(){
        var __success = function (_res) {
            var _ret;
            if(!!_res){
                _ret = _res.ret;
                if(_ret === '420' || _ret === '201'){
                    //201:帐号存在,420:帐号不存在
                    this.data.newAcc = _ret === '420';
                    this.sendSms(this.data.newAcc);
                }else{
                    if(_res.dt == '09'){
                        _ret += '_09';
                    }
                    Toast.show(this.getCodeMsg('NCHN_'+_ret));
                    this.showCaptcha(this.data.capType);
                    this.$refs.btnGetSms.setStatus('enable');
                }
            }
        };
        var __error = function (_res) {
            this.showCaptcha(this.data.capType);
            this.$refs.btnGetSms.setStatus('enable');
        };
        return function(){
            var _data = {
                pd  : pd,
                pkid  : pkid,
                pkht  : pkht,
                channel  : '10',
                mb  : this.$refs.mbInput.getValue(),
                topURL  : location.href
            };
            request({
                url:'/yd/nchn',
                method:'GET',
                data:_data,
                success:__success.bind(this),
                error:__error.bind(this)
            })
        }
    })(),
    /**
     * 发送短信
     * */
    sendSms:(function(){
        var __success = function (_res) {
            var _ret;
            if(!!_res){
                _ret = _res.ret;
                if(_ret === '201'){
                    storage.setSessionStorage('smsCode','201');
                    this.goNext();
                }else if(this.ssmsFirstRq && (_ret === '407' && this.data.newAcc || _ret === '420' && !this.data.newAcc )){
                    //帐号已经存在，切换到登录
                    this.ssmsFirstRq  = false;
                    this.data.newAcc = !this.data.newAcc;
                    this.sendSms(this.data.newAcc,false);
                }else if(_ret === '411' && _res.receiveCode){
                    //上行短信
                    this.data.receiveCode = _res.receiveCode;
                    this.goNext();
                }else if(_ret === '422' || _ret === '602'){
                    //帐号已被锁定、冻结
                    Confirm.show( _ret === '422'?this.$state.AccConfig['lockedAccConfig']:this.$state.AccConfig['frozenAccConfig']);
                    this.showCaptcha(this.data.capType );
                }else {
                    if(_ret === '441'){
                        this.data.capType = 1;
                    }else if(_ret === '444'){
                        this.data.capType = 2;
                    }else if(_ret === '445'){
                        this.data.capType = 3;
                    }else if(_ret === '401' && _res.dt == '10') {
                        _ret += '_10';
                    }
                    Toast.show(this.getCodeMsg('SSMS_'+_ret));
                    this.showCaptcha(this.data.capType );
                }
            }
            this.$refs.btnGetSms.setStatus('enable');
        };
        var __error = function (_res) {
            this.showCaptcha(this.data.capType );
            this.$refs.btnGetSms.setStatus('enable');
        };
        return function(_newAcc,_ssmsFirstRq = true){
            this.ssmsFirstRq = _ssmsFirstRq;
            this.data.receiveCode = "";
            var _url = '/yd/nlssms',
                _data = {
                    pd  : pd,
                    pkid  : pkid,
                    pkht  : pkht,
                    un : this.$refs.mbInput.getValue(),
                    topURL : location.href,
                    channel : '11'
                };
            if(_newAcc){
                _url = '/yd/nregssms';
                _data.channel = '12';
            }
            request({
                url:_url,
                method:'GET',
                data:_data,
                success:__success.bind(this),
                error:__error.bind(this)
            })
        }
    })(),
    /**
     * 根据code获取错误文案
     * */
    getCodeMsg :function (_key) {
        return codeMsg[_key] || "未知错误";
    },
    /**
     * 进入下一步
     * */
    goNext:function () {
        this.keepSUD();
        this.$state.go('app.checkSms');
        this.$refs.btnGetSms.setStatus('enable');
    },
    /**
     * 保持环境
     * */
    keepSUD:function () {
        this.data.capType && storage.setSessionStorage('capType',this.data.capType);
        storage.setSessionStorage('mobile',this.$refs.mbInput.getValue());
        storage.setSessionStorage('opType',this.data.newAcc?'reg':'login');
        if(this.data.receiveCode){
            storage.setSessionStorage('receiveCode',this.data.receiveCode);
        }else{
            storage.removeSessionStorage('receiveCode');
        }
        this.data.noPwd &&  storage.setSessionStorage('noPwd',this.data.noPwd);
    }
});

module.exports = mbLogin;




