var DEFAULTTIME = 2000;
var APIURL = "//10.242.69.181:8002";
var CROSS = false;
var WITHCRED = false;

'use strict';

import './loading.scss';
import Regular from 'regularjs';
import tpl from './loading.html';

let Loading = Regular.extend({
    template:tpl,
    name:'Loading',
    data:{
        type:1,
        loading:true
    },
    init:function(){
    },
    show:function(){
        this.data.loading = true;
        this.$update();
    },
    end:function () {
        this.data.loading = false;
        this.$update();
    }
});

module.exports =  Loading;




