/*** IMPORTS FROM imports-loader ***/
var DEFAULTTIME = 2000;
var APIURL = "//10.242.69.181:8002";
var CROSS = false;
var WITHCRED = false;

'use strict'

import './confirm.scss';
import Regular from 'regularjs';
import tpl from './confirm.html';
let Confirm = Regular.extend({
    template:tpl,
    name:'Confirm',
    data:{
        warnCloseTxt:'取消',
        warnConfirmTxt:'确定'
    },
    init:function(_data){
        this.supr(_data);
    },
    /**
    * _conf:{
    *   info(string):'',
    *   oprate(object):{
    *     button1(object):{
    *       txt(string):'',
    *       func(function):.
    *     },
    *     button2(object):{
    *       txt(string):'',
    *       func(function):.
    *     }
    *   }
    * }
    * */
    show:function(_conf){
        if(typeof _conf == 'string') {
            this.data.confirmCnt = _conf;
        }else{
            this.data.confirmCnt = _conf.info;
            if(_conf.oprate) {
                var _op = _conf.oprate;
                if (!!_op.button1) {
                    var _btnConf = _op.button1;
                    this.data.warnCloseTxt = _btnConf['txt'];
                    this.onClose2 = _btnConf['func'];
                }
                if (!!_op.button2) {
                    var _btnConf = _op.button2;
                    this.data.warnConfirmTxt = _btnConf['txt'];
                    this.onConfirm2 = _btnConf['func'];
                }
            }
        }
        this.$update();
    },
    onClose:function () {
        this.hide();
        if(this.onClose2){
            this.onClose2();
        }
    },
    onConfirm:function () {
        this.hide();
        if(this.onConfirm2){
            this.onConfirm2();
        }
    },
    hide:function () {
        this.data.confirmHide = true;
        setTimeout(()=>{
            this.data.confirmCnt = '';
            this.data.confirmHide = false;
            this.$update();
        },400)
        this.$update();
    }
});

module.exports =  new Confirm({}).$inject(document.body);




