var DEFAULTTIME = 2000;
var APIURL = "//10.242.69.181:8002";
var CROSS = false;
var WITHCRED = false;

'use strict'

import './mbinput.scss'
import tpl from './mbinput.rgl'

var Validation = require('rgui-ui-field').Validation;
var Base = require('../input/input');
var ListView = require('../../../listview/listview');
var Item = require('../../../listview/item/item');
var Dom = require('regularjs').dom;
/*
 * 基础组件-MbInput，继承自ui/input
 * @auth hzhuangdi(hzhuangdi@corp.netease.com)
 * public属性：
 * @param {object} data.selected 
 * => 当前选中的国家名称和区号，默认中国{nm:'CN',value:'86'}
 * @param {string} data.mbValue
 * => 组合后的手机号，格式为：区号+输入框输入的手机号，如86+15066666666，data.value变化和selected变化时都会更新该值
 * @param {bool} data.open
 * => 区号下拉选框开关状态，默认false
 */
var MbInput = Base.extend({
    name: 'MbInput',
    template: tpl,
    config() {
        this.defaults({
            placeholder: '手机号码',
            enableDelete: true,
            selected: {nm:'CN',value:'86'},
            value: '',
            mbValue: '',
            open: false,
            cancelValidate:0,

        });
        this.supr();  
    },
    init() {
        Dom.on(document, 'click', (e) => {
            var element = this.$refs.trigger;
            var element2 = e.target;
            while(element2) {
                if(element == element2)
                    return;
                element2 = element2.parentElement;
            }
            this.toggle(false);
        });
        this.watch();
    },
    watch(){
        this.$watch('value',(value,oldValue)=>{
            this.data.mbValue = this.data.selected.value + '-' + value;
        })
    },
    // @override
    getValue(){
        return this.data.mbValue;
    },
    onSelect (_data){
        this.data.selected = {nm:_data.selected.data.nm,value:_data.value};
        this.data.mbValue = this.data.selected.value + '-' + this.data.value;
        this.$update();
        this.toggle(false);
        this.data.cancelValidate = 0;
        
        if(this.data.value === ''){
            return;
        }
        this.validate('blur');

    },
    _toggle(_open){
        // 取消掉blur校验？

        this.data.cancelValidate = 1;
        this.toggle(_open);        
    },
    toggle (_open){
        this.data.open = _open;
        this.$update();
    },
    // @override    
    _onBlur(_event) {
        var _blur = this.data.blurDelay>34?this.data.blurDelay:34;

        this.$emit('blur',_event);
        
        if(this.data.blurDelay){
            setTimeout(()=>{
                if(this.data.value === ''){
                    return;
                }
                if(this.data.cancelValidate){
                    this.data.cancelValidate = 0;
                    return;
                }
                this.validate('blur');
            },_blur);
        }else{
            if(this.data.value === ''){
                return;
            }
            if(this.data.cancelValidate){
                this.data.cancelValidate = 0;
                return;
            }
            this.validate('blur');
        }
    },
    // @override
    validate(trigger = 'submit') {

        const value = this.data.mbValue;
        const rules = this.data.rules.filter((rule) => (rule.trigger + '+submit').includes(trigger));

        // this.data._state = this.data.state = 'validating';
        // 先不维护_state
        this.data.state = 'validating';

        Validation.validate(value, rules, (result) => {
            if (result.firstRule && !(result.firstRule.mute || '').includes(trigger))
                // @TODO tip根据结果？
                this.data._tip = result.message;
            else
                this.data._tip = '';
            // 先不维护_state
            // this.data._state = this.data.state = result.success ? 'success' : 'error';
            this.data.state = result.success ? 'success' : 'error';
            
            this.$update();

            /**
             * @event validate 验证表单域时触发
             * @property {object} sender 事件发送对象
             * @property {string} trigger 验证触发方式
             * @property {boolean} success 验证是否通过
             * @property {string} message 验证不通过时的消息
             * @property {object} firstRule 第一条验证不通过的规则
             */
            this.$emit('validate', Object.assign({
                sender: this,
                trigger,
            }, result));
        });
    },
});

module.exports = MbInput;




